h1{
    height: 10vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ProjectWrapper{
    position: absolute;
    top: 8vh; left: 0;
    width: 100vw;
    display: grid;
}

.ProjMain{
    position: relative;
    left: 5%;
    width: 90%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
    grid-auto-rows: 1fr;
    grid-gap: 2rem;
}